import Pact from 'pact-lang-api'
import { ref } from 'vue'
import ENUMS from './components/Enums'

var pretty_minutes = function (x) {
    if (x < 60) {
        return x.toFixed(2) + "seconds"
    }
    else if (x < 60 * 60) {
        return (x / 60).toFixed(2) + " minutes"
    }
    else if (x < 60 * 60 * 24) {
        return (x / 60 / 60).toFixed(2) + " hours"
    }
    return (x / 60 / 60 / 24).toFixed(2) + " days"
}

const shorten_account = function (x) {
    if (x.length > 2) {
        if (x.substr(0, 2) == "k:") {
            return x.substr(0, 6) + "..." + x.substr(x.length - 2, 2)
        } else if (x.length > 10) {
            return x.substr(0, 6) + "..." + x.substr(x.length - 2, 2)
        }
    }
    return x
}

const get_nonce = function () {
    return new Date().toUTCString()
}


