const mymeta = function (acc, c_id) {
    // var acc = get_account_name(qthis)
    if (vget2(acc) == undefined)
        return Pact.lang.mkMeta("", c_id, 0.000001, 2e6, Math.round((new Date).getTime() / 1e3) - 10, 600)
    return Pact.lang.mkMeta(acc, c_id, 0.000001, 2e6, Math.round((new Date).getTime() / 1e3) - 10, 600)

}



var mpad = function (x) {
    while (x.length < 8) {
        x = '0' + x
    }
    return x
}

var date_formatter = function (x) {
    if (typeof x === 'object') {
        if ("timep" in x)
            return x.timep.split("T")[0]
        return x.time.split("T")[0]
    } else {
        // TODO handle error
    }
}

var countzeros = function (x) {
    var c = 0
    while (x[c] == '0') {
        c += 1
    }
    return c
}

var get_nft_strength_from_hash = function (x) {
    return countzeros(Array.from(Pact.crypto.base64UrlDecodeArr(x)).map((x) => mpad(x.toString(2))).reduce((x, y) => x + y))
}

var get_nft_strength_with_nonce = function (a, b) {
    return get_nft_strength_from_hash(Pact.crypto.hash(a + b))
}

var transfer_cap_maker = function (payer, dbts) {
    var tmp = Object.entries(dbts).filter((x) => x[1] > 0)
    var res = []
    for (var i in tmp) {
        res.push({
            "cap": {
                "name": "coin.TRANSFER",
                "args": [payer, tmp[i][0], parseFloat(tmp[i][1].toFixed(10))]
            }
        })
    }
    return res
}


var calc_no_front_run = function (nfr) {
    var nfrtmp = nfr.nft.get_id()
        + ":" +
        nfr.nft.get_curhash()
        + ":" +
        nfr.strongest_nonce_found
    var res = Pact.crypto.hash(nfrtmp)
    return res

}
var mypolling2 = function (qthis, reqkey, x, queryitem, apihosttmp) {
    return Pact.fetch.poll(queryitem.get_request_key(), apihosttmp).catch(x => { throw x }).then((res) => {
        if (Object.keys(res).length === 0) {
            vsetter(queryitem, "polls", vget(queryitem, "polls") + 1)
            return new Promise((resolve, reject) => {
                setTimeout(() => {
                    mypolling2(qthis, reqkey, x, queryitem, apihosttmp)
                        .catch(x => reject(x))
                        .then(x => resolve(x))
                }, 3000);
            }).catch(x => { throw x })
        } else {
            // qthis.$store.state.query_manager.
            // queryitem.try_set("status", ENUMS.QUERY_ITEM_STATUS.DONE)

            setTimeout(() => {
                get_wallet_manager(qthis).then(wal => wal.get_balance())

            }, 2000);

            return new PollingSuccess(qthis, queryitem, res)
        }
    })
}
var mypolling = function (qthis, reqkey, x) {
    var qi = new QueryItem(x, reqkey)
    qthis.$store.state.query_manager.add_query(qi)

    return get_wallet_manager(qthis).catch(x => { throw x }).then(wal => {
        var apihosttmp = wal.get_api_host()

        // console.log("_+_", x, queryitem.get_request_key())
        return mypolling2(qthis, reqkey, x, qi, apihosttmp)
    }).catch(e => {
        qi.try_set("status", ENUMS.QUERY_ITEM_STATUS.FAIL)
        throw e
    }).then(x => {
        qi.try_set("status", ENUMS.QUERY_ITEM_STATUS.DONE)

        return x
    })
}

var save_found_nonces = function (qthis) {
    if (get_nft_strength_with_nonce(qthis.nft.get_curhash(), vget(qthis, "strongest_nonce_found")) > qthis.nft.get_strength()) {
        qthis.appli.$store.state.found_nonces[qthis.nft.get_id()] = { nonce: vget(qthis, "strongest_nonce_found"), hashfor: qthis.nft.get_curhash() }
    }
    localStorage.found_nonces = JSON.stringify(qthis.appli.$store.state.found_nonces)
}

var delete_found_nonces = function (qthis, nft) {
    delete qthis.$store.state.found_nonces[nft.get_id()]
    localStorage.found_nonces = JSON.stringify(qthis.$store.state.found_nonces)
}

var EMOPOOLS = {}
var EMOLOOKUP = {}
setTimeout(() => {
    for (var i1 in ENUMS.EMOJI2) {
        EMOPOOLS[i1] = Object.keys(ENUMS.EMOJI2[i1]).map(x => Pact.crypto.hash(ENUMS.EMOJI2[i1][x])).sort()
    }


    for (var i in ENUMS.EMOJI2) {
        for (var j in ENUMS.EMOJI2[i]) {

            EMOLOOKUP[Pact.crypto.hash(ENUMS.EMOJI2[i][j])] = ENUMS.EMOJI2[i][j]
        }
    }

}, 1);



class NFTItem {
    appli;
    id;
    data;
    constructor(appli, x) {
        this.appli = appli
        this.id = x.id.int
        this.data = x
        if (appli.$store.state.found_nonces[this.get_id()] != undefined) {
            if (appli.$store.state.found_nonces[this.get_id()].hashfor != this.get_curhash()) {
                delete_found_nonces(this.appli, this)
            }
        }
    }
    is_in_grace() {
        if (new Date() - new Date(this.get_mint_date()) < 7 * 24 * 60 * 60 * 1000) {
            return true
        }
        return false
    }
    calc_emoj(x) {
        var mkey = Pact.crypto.hash(x + ":" + this.get_attrib())

        var c = 0;
        for (var i = 0; i < EMOPOOLS[x].length - 1; i++) {
            if ((mkey >= EMOPOOLS[x][i]) && (mkey < EMOPOOLS[x][i + 1])) {
                c = i
                break
            }
        }

        return EMOLOOKUP[EMOPOOLS[x][c]]

    }
    buy_nft() {
        var qthis = this

        var debtors = {

        }

        return get_account(qthis.appli).then(acc => {


            debtors[qthis.appli.$store.state.COAL_MAKER] = 0
            debtors[qthis.get_full_minter()] = 0
            debtors[qthis.get_full_owner()] = 0

            debtors[qthis.appli.$store.state.COAL_MAKER] += qthis.get_price() * 0.0201
            debtors[qthis.get_full_minter()] += qthis.get_price() * 0.0201
            debtors[qthis.get_full_owner()] += qthis.get_price() * 0.9601

            if (acc.name == qthis.get_full_minter()) {
                delete debtors[acc.name]
            }



            var cap_dbt = transfer_cap_maker(acc.name, debtors)


            var caps_buy = [
                {
                    "cap": {
                        "name": "coin.GAS",
                        "args": []
                    }
                }
            ]

            caps_buy = caps_buy.concat(cap_dbt)


            return get_wallet_manager(qthis.appli).then(wal => {
                var signingCmd = {
                    "pactCode": '(free.coal.buy-coal "' + qthis.get_id() + '")',
                    "caps": caps_buy,// nft.minter != qthis.$store.state.account.account ? caps_buy : caps_buy_minterbuyer,
                    "sender": acc.name,
                    "ttl": 600,
                    "gasLimit": qthis.appli.$store.state.settings.GAS_LIMIT,
                    "chainId": vget(wal, "chain_id"),
                    "gasPrice": 0.000001,
                    "envData": {
                        "coal-ks": [acc.public_key],
                        //   "account": localStorage.account
                    },
                    "signingPubKey": acc.public_key,
                    "networkId": vget(wal, "network_id")
                }
                return wal.sign(signingCmd).then(xsigned => {
                    return wal.send_local_signed(xsigned)
                        .catch(x => { throw x })
                        .then(() => wal.send(xsigned))
                        .catch(x => { throw x })
                        .then(x => { return new BuyNFT(qthis.appli, x) })
                })
            })

        });
    }
    set_remote_price(new_price, new_onsale, new_saleend) {
        var qthis = this
        return get_account(qthis.appli).then(acc => {

            return get_wallet_manager(qthis.appli).then(x => {
                var signingCmd = {
                    "pactCode": '(free.coal.set-sale-state "' + qthis.get_id() + '" ' + calc_price(new_price) + ' ' + Boolean(new_onsale) + ' ' + calc_price(new_saleend) + ')',
                    "caps": [],
                    "sender": acc.name,
                    "ttl": 600,
                    "gasLimit": qthis.appli.$store.state.settings.GAS_LIMIT,
                    "chainId": vget(x, "chain_id"),
                    "gasPrice": 0.000001,
                    "signingPubKey": [acc.public_key],
                    "networkId": vget(x, "network_id")
                }
                return x.sign(signingCmd).then(y => x.send(y))
            }).then(x => {
                return new SetNFTPrice(qthis.appli, x)
            })
        })
    }

    is_owned_by_wallet() {
        if (this.appli.$store.state.wallet_manager == undefined)
            return false
        return this.data.owner == this.appli.$store.state.wallet_manager.account_name
    }
    get_id() {
        return this.id
    }
    get_owner() {
        return shorten_account(this.data.owner)
    }
    get_strength() {
        return get_nft_strength_from_hash(this.data.curhash)
    }
    get_strength_with_nonce(nonce) {
        return get_nft_strength_from_hash(Pact.crypto.hash(this.data.curhash + nonce))
    }

    get_price() {
        return this.data.price
    }
    get_price_formatted() {
        return this.data.price
    }
    get_sale_state() {
        return this.data["on-sale"]
    }
    get_minter() {
        return shorten_account(this.data.minter)
    }
    get_date_formatted() {
        return date_formatter(this.data["mint-date"])
    }
    get_mint_date() {
        if (typeof this.data["mint-date"] == "object") {
            return Object.values(this.data["mint-date"])[0]
        }
        return this.data["mint-date"]

    }
    get_remaining_grace() {
        var res = new Date() - new Date(this.get_mint_date())
        res /= 1000
        return pretty_minutes(7 * 24 * 60 * 60 - res)

    }
    get_curhash() {
        return this.data["curhash"]
    }
    get_attrib() {
        return this.data["attrib"]
    }
    get_full_owner() {
        return this.data.owner
    }
    get_full_minter() {
        return this.data.minter
    }
}

var get_wallet_manager = function (qthis) {

    return new Promise(function (resolve) {
        if (qthis.$store.state.wallet_manager == undefined) {
            // reject()
            resolve(new WalletManager())
        } else {
            qthis.$store.state.wallet_manager.get_current_wallet_network().then(() => {
                resolve(qthis.$store.state.wallet_manager)

            })

        }
    })
}

var get_nft_manager = function (qthis) {
    return new Promise(function (resolve) {
        resolve(qthis.$store.state.nft_manager)
    })
}

var get_account = function (qthis) {
    return get_wallet_manager(qthis).then((x) => x.get_account_details())
}

var calc_price = function (t) {
    var res = 0.0
    if (t == '') {
        res = this.nft.price
    }
    else if (t == undefined) {
        res = this.nft.price
    } else {
        res = t
    }
    res = res + ""
    if (res.indexOf(".") > -1) {
        return res
    }
    return parseFloat(res + "").toFixed(1)
}

class MineItem {
    appli;
    nft;
    run_state;
    worker;
    strongest_nonce_found;
    strongest_nonce_found_strength;
    decoy_nonce;

    constructor(aapl, nft) {
        this.appli = aapl
        this.nft = nft

        this.run_state = ref(ENUMS.WORKER.STOPPED)
        this.strongest_nonce_found = ref(undefined)
        this.strongest_nonce_found_strength = ref(nft.get_strength())
        if (this.appli.$store.state.found_nonces[this.nft.get_id()] != undefined) {
            if (this.appli.$store.state.found_nonces[this.nft.get_id()].hashfor == this.nft.get_curhash()) {

                vsetter(this, "strongest_nonce_found", (this.appli.$store.state.found_nonces[this.nft.get_id()].nonce))
                vsetter(this, "strongest_nonce_found_strength", (get_nft_strength_with_nonce(this.nft.get_curhash(), vget(this, "strongest_nonce_found"))))
            }
        }
        this.decoy_nonce = ref("")


        this.initialize()
        this.set_hash()
        this.continue()

    }
    initialize() {
        var qthis = this
        vsetter(this, "run_state", ENUMS.WORKER.RUNNING)

        if (this.worker == undefined) {
            this.worker = new Worker('/miner.js')

            this.worker.onmessage = function (evv) {
                var ev = evv.data
                if (ev.res == "found") {
                    vsetter(qthis, "run_state", ENUMS.WORKER.STOPPED)

                    vsetter(qthis, "strongest_nonce_found", ev.data.nonce)

                    vsetter(qthis, "strongest_nonce_found_strength", qthis.nft.get_strength_with_nonce(ev.data.nonce))
                    save_found_nonces(qthis)

                } else if (ev.res == "notfoundyet") {
                    vsetter(qthis, "decoy_nonce", ev.data)
                    qthis.worker.postMessage(JSON.stringify({ type: "continue" }))

                }
            }

        }
    }
    close_mine() {
        if (this.worker != undefined)
            this.worker.terminate()
        this.worker = undefined
        vsetter(this, "run_state", ENUMS.WORKER.STOPPED)
    }
    continue() {
        if (this.worker == undefined) {
            this.initialize()
        }

        this.worker.postMessage(JSON.stringify({ type: "continue" }))
        vsetter(this, "run_state", ENUMS.WORKER.RUNNING)

    }
    set_hash() {
        this.worker.postMessage(JSON.stringify({
            type: "set_hash", data: {
                curhash: this.nft.get_curhash()
                , strongest_nonce: vget(this, "strongest_nonce_found")
                , strongest_nonce_found_strength: vget(this, "strongest_nonce_found_strength")
            }
        }))
    }

}
