class Output_M {
    gas;
    status;
    data;
    constructor() {
    }
    get_data() {
        return this.data
    }
    get_status() {
        return this.status
    }
}
class SendOutput extends Output_M {
    constructor(x) {
        super()
        if (Object.keys(x).length != 1) {
            throw new Error("Not implemented")
        } else {
            var tmp = Object.values(x)[0]
            this.gas = tmp.gas
            if (tmp.result.error != undefined) {
                throw new OutputError_M(x)
            } else {
                this.status = tmp.result.status
                this.data = tmp.result.data
            }
        }
    }
}
class LocalOutput_M extends Output_M {
    constructor(x) {
        super()
        console.log("000000", x)
        if (x.fetchResult.result.status != "success") {
            var tmp = x.fetchResult.result.error.message.toLowerCase()
            if (tmp.indexOf("insufficient funds") > -1) {
                throw new InsufficientFundsError(x)
            } else if ((tmp.indexOf("gas limit") > -1) && (tmp.indexOf("exceed") > -1)) {
                throw new GasLimitExceedError(x)
            } else if (tmp.indexOf("keyset failure") > -1) {
                throw new KeysetFailure(x)
            }
            throw new OutputError_M(x)
        }
        this.gas = x.fetchResult.gas
        this.status = x.fetchResult.result.status
        this.data = x.fetchResult.result.data
    }
}
