var remove_all = function (a, b) {
    while (a.indexOf(b) > -1) {
        a = a.replace(b, "")
    }
    return a
}

class QueryItem {
    hash;
    importance;
    polls;
    mcmd;
    status;
    reason;
    mtime;
    reqkey;

    get_time() {
        return this.mtime
    }

    try_set(mk, mv) {
        if (mk == "status" && vget(this, "status") == ENUMS.QUERY_ITEM_STATUS.FAIL) return
        var qthis = this
        vsetter(qthis, mk, mv)
    }

    get_request_key() {
        return JSON.parse(this.reqkey)
    }
    constructor(xcmd, reqkey) {
        this.importance = ENUMS.QUERY_IMPORTANCE.NOT_IMPORTANT
        this.polls = ref(0)
        this.mtime = new Date().getTime()
        this.status = ref(ENUMS.QUERY_ITEM_STATUS.LOADING)
        this.reason = ref("")
        this.reqkey = reqkey
        console.log("||||--||||", reqkey, xcmd)
        // var x = xx
        // try {
        //     x = JSON.parse(x)
        // } catch (error) {
        //     x = xx
        // }

        var tmpl = xcmd.xcmd.signedCmd.cmd.split("free.coal.")[1]
        var a = tmpl.indexOf(")")
        var b = tmpl.indexOf(" ")
        var func = tmpl.slice(0, Math.min(a, b))
        var args = tmpl.slice(Math.min(a, b), a).split(" ")
        args = args.filter(x => x != '')



        if (func.startsWith("set-sale-")) {
            func = "set price"
        } else if (func.startsWith("mint")) {
            func = "minting"
        } else if (func.startsWith("lock-nfr")) {
            func = "locking nfr"
        } else if (func.startsWith("verify-nfr")) {
            func = "verifying nfr"
        } else if (func.startsWith("submit-nfr")) {
            func = "submitting nfr"
        } else if (func.startsWith("steal")) {
            func = "stealing nft"
        } else if (func.startsWith("buy")) {
            func = "buying nft"
        }

        this.mcmd = func



        if (args.length > 0) {
            var tmpi = args[0]
            if (tmpi.indexOf("\"") > 0) {
                tmpi = remove_all(tmpi, "\"")
                tmpi = remove_all(tmpi, "\\")
            }
            if (func == "set price") {
                if (args[2] == 'false') {
                    this.mcmd = "remove sale of #" + tmpi
                } else {
                    this.mcmd += " of #" + tmpi + " to " + args[1]
                }
            }
            else if (func == "locking nfr")
                this.mcmd += " for #" + tmpi
            else if (func == "stealing nft")
                this.mcmd += " #" + tmpi
            else if (func == "buying nft")
                this.mcmd += " #" + tmpi // TODO add price


        }


        // if (x.requestKeys != undefined) {
        //     this.hash = x.requestKeys[0]

        // } else {
        //     console.log("QIerror", typeof x)
        //     console.log(Object.keys(x))
        // }

    }

}

